import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { StickersAllItem } from "../items";
import {
  dataAnime,
  dataCartoons,
  dataFilms,
  dataGames,
  dataTVSeries,
  SocketManager,
} from "../helpers";
import { ModalStickers } from "../modals";

const { callMethod } = SocketManager;

const Shop = ({
  id,
  networkStatus,
  updateConnection,
  stub,
  snackbar,
  setActiveModal,
  openPaymentSystem,
}) => {
  let targetId = 0;
  let dataStickersAll = [];
  const [activeTab, setActiveTab] = useState("all-stickers");
  const [slideIndex, setSlideIndex] = useState(1);

  // RENDERS
  const [renderDataStickersAll, setRenderDataStickersAll] = useState([]);

  dataStickersAll = [...dataCartoons, ...dataFilms, ...dataTVSeries];

  function openModalStickers(e) {
    targetId = +e.currentTarget.id;

    const {
      name,
      author,
      description,
      tags,
      price,
      icon,
      amount,
    } = dataStickersAll.find((stickers) => stickers.id === targetId);

    setActiveModal(
      <ModalStickers
        name={name}
        price={price}
        icon={icon}
        author={author}
        description={description}
        tags={tags}
        amount={amount}
        setActiveModal={setActiveModal}
        openPaymentSystem={openPaymentSystem}
      />
    );
  }

  function tab(type) {
    setActiveTab(type);
    window.scrollTo(0, 0);

    getAllStickers(type === "all-stickers" ? [] : [type]);
  }

  function getAllStickers(tags = []) {
    callMethod("getStickerPacks", {
      tags,
    }).then(({ status, message }) => {
      setRenderDataStickersAll(
        message.map((item, index) => {
          return (
            <StickersAllItem
              key={index}
              id={index + 1}
              name={item.name}
              authorId={item.authorId}
              authorName={item.authorName}
              description={item.description}
              price={item.price}
              icon={item.icon}
              amount={item.amount}
              openModalStickers={openModalStickers}
              openPaymentSystem={openPaymentSystem}
            />
          );
        })
      );
    });
  }

  useEffect(() => {
    getAllStickers();
  }, []);

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>VIP Стикеры</PanelHeaderSimple>

      <Gallery
        // timeout="2000"
        slideIndex={slideIndex}
        onChange={(slideIndex) => setSlideIndex(slideIndex)}
        slideWidth="80%"
        align="center"
        style={{
          height: "180px",
          marginTop: "10px",
        }}
      >
        {/* <Div id="0" onClick={openModalStickers} className="iconSliderNaruto"></Div> */}
        <Div
          id="2"
          onClick={openModalStickers}
          className="iconSliderJoker"
        ></Div>
        <Div
          id="3"
          onClick={openModalStickers}
          className="iconSliderMatrix"
        ></Div>
        <Div
          id="4"
          onClick={openModalStickers}
          className="iconSliderMinecraft"
        ></Div>
      </Gallery>

      <>
        <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
              margin: 10,
              userSelect: "none",
              marginRight: 20,
              marginLeft: 20,
            }}
          >
            <Div
              onClick={() => tab("all-stickers")}
              className={activeTab === "all-stickers" ? "tabGreen" : "tabWhite"}
            >
              Все
            </Div>
            <Div
              onClick={() => tab("anime")}
              className={activeTab === "anime" ? "tabGreen" : "tabWhite"}
            >
              Аниме
            </Div>
            <Div
              onClick={() => tab("games")}
              className={activeTab === "games" ? "tabGreen" : "tabWhite"}
            >
              Игры
            </Div>
            <Div
              onClick={() => tab("cartoons")}
              className={activeTab === "cartoons" ? "tabGreen" : "tabWhite"}
            >
              Мультики
            </Div>
            <Div
              onClick={() => tab("films")}
              className={activeTab === "films" ? "tabGreen" : "tabWhite"}
            >
              Фильмы
            </Div>
            <Div
              onClick={() => tab("tv-series")}
              className={activeTab === "tv-series" ? "tabGreen" : "tabWhite"}
            >
              Сериалы
            </Div>
          </Div>
        </HorizontalScroll>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            <>
              {activeTab === "all-stickers" ? (
                <>
                  <Div>
                    <>
                      {!renderDataStickersAll.length ? (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      ) : (
                        <Div
                          style={{
                            paddingLeft: 10,
                            paddingRight: 10,
                          }}
                        >
                          {" "}
                          {renderDataStickersAll}
                        </Div>
                      )}
                    </>
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "anime" ? (
                <>
                  <Div
                    style={{
                      paddingLeft: 10,
                      paddingRight: 10,
                    }}
                  >
                    {renderDataStickersAll}
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "games" ? (
                <>
                  <Div
                    style={{
                      paddingLeft: 10,
                      paddingRight: 10,
                    }}
                  >
                    {renderDataStickersAll}
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "cartoons" ? (
                <>
                  <Div
                    style={{
                      paddingLeft: 10,
                      paddingRight: 10,
                    }}
                  >
                    {renderDataStickersAll}
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "films" ? (
                <>
                  <Div
                    style={{
                      paddingLeft: 10,
                      paddingRight: 10,
                    }}
                  >
                    {renderDataStickersAll}
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "tv-series" ? (
                <>
                  <Div
                    style={{
                      paddingLeft: 10,
                      paddingRight: 10,
                    }}
                  >
                    {renderDataStickersAll}
                  </Div>
                </>
              ) : (
                ""
              )}
            </>

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Shop };
