import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { Statistics } from "../components";

const GroupInfoStatistic = ({
  id,
  go,
  networkStatus,
  updateConnection,
  stub,
  snackbar,
  openAlert,
}) => {
  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
      left={
        <PanelHeaderBack onClick={go} data-to="profile" />
      }
      >Статистика по группе</PanelHeaderSimple>

      <>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            <Div>
              <Statistics />
            </Div>
            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { GroupInfoStatistic };
